import React, { useState, useEffect } from 'react';
import { recruiterAPI } from '../services/apiService';
import '../recruiter.css';

const CreateJobPage = ({ onBack, onJobCreated, recruiterId }) => {
  // Load data from localStorage on component mount
  const loadSavedData = () => {
    const savedData = localStorage.getItem('createJobFormData');
    if (savedData) {
      try {
        return JSON.parse(savedData);
      } catch (error) {
        console.error('Error parsing saved form data:', error);
        return null;
      }
    }
    return null;
  };

  const [jobData, setJobData] = useState(() => {
    const savedData = loadSavedData();
    return savedData || {
      title: '',
      description: '',
      company: '',
      status: 'Active',
      ticket: {
        location: '',
        experience: 0,
        remote: 'No',
        jobType: 'Full-time',
        field: ''
      }
    };
  });

  const [errors, setErrors] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');

  // Save data to localStorage whenever jobData changes
  useEffect(() => {
    localStorage.setItem('createJobFormData', JSON.stringify(jobData));
  }, [jobData]);

  // Note: We don't clean up localStorage on page unload
  // because we want to preserve data on refresh
  // Data will only be cleared when user explicitly cancels or creates job successfully

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    
    if (name.startsWith('ticket.')) {
      const ticketField = name.split('.')[1];
      setJobData(prev => ({
        ...prev,
        ticket: {
          ...prev.ticket,
          [ticketField]: ticketField === 'experience' ? parseInt(value) || 0 : value
        }
      }));
    } else {
      setJobData(prev => ({
        ...prev,
        [name]: value
      }));
    }
    
    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!jobData.title.trim()) {
      newErrors.title = 'Job title is required';
    }
    
    if (!jobData.description.trim()) {
      newErrors.description = 'Job description is required';
    }
    
    if (!jobData.company.trim()) {
      newErrors.company = 'Company name is required';
    }
    
    if (!jobData.ticket.location.trim()) {
      newErrors['ticket.location'] = 'Location is required';
    }
    
    if (!jobData.ticket.field.trim()) {
      newErrors['ticket.field'] = 'Field is required';
    }
    
    if (jobData.ticket.experience < 0) {
      newErrors['ticket.experience'] = 'Experience must be 0 or greater';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSave = async () => {
    if (!validateForm()) {
      return;
    }
    
    setIsLoading(true);
    setSuccessMessage('');
    
    try {
      const jobToSave = {
        ...jobData,
        recruiterId: recruiterId
      };
      
      const result = await recruiterAPI.createJob(jobToSave);
      
      if (result.success) {
        setSuccessMessage('Job created successfully!');
        // Clear localStorage and reset form
        localStorage.removeItem('createJobFormData');
        setJobData({
          title: '',
          description: '',
          company: '',
          status: 'Active',
          ticket: {
            location: '',
            experience: 0,
            remote: 'No',
            jobType: 'Full-time',
            field: ''
          }
        });
        setErrors({});
        
        // Auto redirect after 2 seconds
        setTimeout(() => {
          localStorage.removeItem('isOnCreateJobPage');
          // Navigate back to recruiter portal
          window.location.href = `${window.location.origin}${window.location.pathname}?page=profile&user=recruiter&id=${recruiterId}`;
        }, 2000);
      } else {
        setErrors({ general: result.message || 'Failed to create job' });
      }
    } catch (error) {
      console.error('Failed to create job:', error);
      setErrors({ general: 'Failed to create job' });
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    // Clear localStorage when canceling
    localStorage.removeItem('createJobFormData');
    localStorage.removeItem('isOnCreateJobPage');
    // Navigate back to recruiter portal
    window.location.href = `${window.location.origin}${window.location.pathname}?page=profile&user=recruiter&id=${recruiterId}`;
  };

  return (
    <div className="create-job-page">
      <div className="page-header">
        <button className="back-btn" onClick={handleCancel}>
          <i className="fas fa-arrow-left"></i> Go Back to Portal
        </button>
        <h1>Create New Job Posting</h1>
      </div>

      <div className="page-content">
        {successMessage && (
          <div className="success-message">
            <i className="fas fa-check-circle"></i>
            {successMessage}
          </div>
        )}

        {errors.general && (
          <div className="error-message">
            <i className="fas fa-exclamation-triangle"></i>
            {errors.general}
          </div>
        )}

        <div className="auto-save-indicator">
          <i className="fas fa-save"></i>
          <span>Your progress is automatically saved</span>
        </div>

        <div className="form-container">
          <div className="form-section">
            <h2>Job Information</h2>
            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Job Title *</label>
                <input
                  type="text"
                  className={`form-input ${errors.title ? 'error' : ''}`}
                  name="title"
                  value={jobData.title}
                  onChange={handleInputChange}
                  placeholder="Enter job title"
                />
                {errors.title && <span className="error-message">{errors.title}</span>}
              </div>
              
              <div className="form-group">
                <label className="form-label">Company *</label>
                <input
                  type="text"
                  className={`form-input ${errors.company ? 'error' : ''}`}
                  name="company"
                  value={jobData.company}
                  onChange={handleInputChange}
                  placeholder="Enter company name"
                />
                {errors.company && <span className="error-message">{errors.company}</span>}
              </div>
            </div>
            
            <div className="form-group">
              <label className="form-label">Job Description *</label>
              <textarea
                className={`form-textarea ${errors.description ? 'error' : ''}`}
                name="description"
                value={jobData.description}
                onChange={handleInputChange}
                placeholder="Describe the job position, responsibilities, and requirements"
                rows="6"
              />
              {errors.description && <span className="error-message">{errors.description}</span>}
            </div>
            
            <div className="form-group">
              <label className="form-label">Status</label>
              <select
                className="form-select"
                name="status"
                value={jobData.status}
                onChange={handleInputChange}
              >
                <option value="Active">Active</option>
                <option value="Closed">Closed</option>
                <option value="Draft">Draft</option>
              </select>
            </div>
          </div>
          
          <div className="form-section">
            <h2>Job Requirements</h2>
            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Location *</label>
                <input
                  type="text"
                  className={`form-input ${errors['ticket.location'] ? 'error' : ''}`}
                  name="ticket.location"
                  value={jobData.ticket.location}
                  onChange={handleInputChange}
                  placeholder="e.g., Tel Aviv, Remote, Hybrid"
                />
                {errors['ticket.location'] && <span className="error-message">{errors['ticket.location']}</span>}
              </div>
              
              <div className="form-group">
                <label className="form-label">Experience (Years) *</label>
                <input
                  type="number"
                  className={`form-input ${errors['ticket.experience'] ? 'error' : ''}`}
                  name="ticket.experience"
                  value={jobData.ticket.experience}
                  onChange={handleInputChange}
                  min="0"
                  placeholder="0"
                />
                {errors['ticket.experience'] && <span className="error-message">{errors['ticket.experience']}</span>}
              </div>
            </div>
            
            <div className="form-row">
              <div className="form-group">
                <label className="form-label">Remote Work</label>
                <select
                  className="form-select"
                  name="ticket.remote"
                  value={jobData.ticket.remote}
                  onChange={handleInputChange}
                >
                  <option value="No">No</option>
                  <option value="Yes">Yes</option>
                  <option value="Hybrid">Hybrid</option>
                </select>
              </div>
              
              <div className="form-group">
                <label className="form-label">Job Type</label>
                <select
                  className="form-select"
                  name="ticket.jobType"
                  value={jobData.ticket.jobType}
                  onChange={handleInputChange}
                >
                  <option value="Full-time">Full-time</option>
                  <option value="Part-time">Part-time</option>
                  <option value="Contract">Contract</option>
                  <option value="Freelance">Freelance</option>
                </select>
              </div>
            </div>
            
            <div className="form-group">
              <label className="form-label">Field *</label>
              <input
                type="text"
                className={`form-input ${errors['ticket.field'] ? 'error' : ''}`}
                name="ticket.field"
                value={jobData.ticket.field}
                onChange={handleInputChange}
                placeholder="e.g., Software Development, Marketing, Sales"
              />
              {errors['ticket.field'] && <span className="error-message">{errors['ticket.field']}</span>}
            </div>
          </div>
        </div>

        <div className="form-actions">
          <button className="btn btn-secondary" onClick={handleCancel} disabled={isLoading}>
            <i className="fas fa-times"></i> Cancel
          </button>
          <button className="btn btn-primary" onClick={handleSave} disabled={isLoading}>
            {isLoading ? (
              <>
                <i className="fas fa-spinner fa-spin"></i> Creating...
              </>
            ) : (
              <>
                <i className="fas fa-plus"></i> Create Job
              </>
            )}
          </button>
        </div>
      </div>
    </div>
  );
};

export default CreateJobPage;
